import React, { useState, useEffect } from 'react'
import rehypeReact from 'rehype-react'

import Typography from '@material-ui/core/Typography'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import TextField from '@material-ui/core/TextField'
import Checkbox from '@material-ui/core/Checkbox'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import ListItemText from '@material-ui/core/ListItemText'
import LinearProgress from '@material-ui/core/LinearProgress'

const DynamicListItem = ({ children, className, onChange, ...props }) => {
  const [checked, setChecked] = useState(false)

  useEffect(() => {
    if (onChange) {
      onChange(checked)
    }
  }, [checked])

  const render = () => {
    let content = undefined

    switch (className) {
      case 'task-list-item':
        const TaskCheckbox = React.createElement(Checkbox, {
          ...children[0].props,
          onChange: e => setChecked(e.target.checked),
          value: checked,
          disabled: false,
        })
        const label = children[1]
        content = (
          <ListItem dense {...props}>
            <FormControlLabel control={TaskCheckbox} label={label} />
          </ListItem>
        )
        break
    }
    return (
      content || (
        <ListItem dense={true}>
          <ListItemText>{children}</ListItemText>
        </ListItem>
      )
    )
  }

  return render()
}

const DynamicList = ({ children, className, progress, ...props }) => {
  const [checked, setChecked] = useState({})
  const handleChange = (name, value) => {
    setChecked({ ...checked, [name]: value })
  }

  const cleanChildrenArray = React.Children.toArray(children).filter(
    child => !!child.props
  )

  const tasks = cleanChildrenArray.filter(
    child => child.props.className === 'task-list-item'
  )
  const renderProgress = cleanChildrenArray.length === tasks.length
  const content = (
    <>
      {}
      {progress && renderProgress && (
        <LinearProgress
          variant="determinate"
          value={
            (Object.values(checked).filter(checked => !!checked).length /
              tasks.length) *
            100
          }
        />
      )}
      <List {...props}>
        {React.Children.map(cleanChildrenArray, (child, index) => {
          const name = `DynamicList-${index}`
          return React.cloneElement(child, {
            onChange: v => handleChange(name, v),
            key: name,
          })
        })}
      </List>
    </>
  )
  return content
}
DynamicList.defaultProps = {
  progress: true,
}

const muiComponentMapping = {
  h3: props => <Typography variant="h4" {...props} />,
  ul: DynamicList,
  ol: List,
  li: DynamicListItem,
  // input: TextField
}

const Markdown = ({
  data: { html, htmlAst },
  rehypeConfig,
  disableMapping,
}) => {
  if (disableMapping === true) {
    return <div dangerouslySetInnerHTML={{ __html: html }} />
  }
  let customMapping = {}
  let customConfig = {}
  if (rehypeConfig) {
    const { components, ...config } = rehypeConfig
    customMapping = components
    customConfig = config
  }
  const renderAst = new rehypeReact({
    createElement: React.createElement,
    // (type, props, children) => {
    //   if (true) {
    //     return React.createElement(type, props, children)
    //   }
    // }
    ...customConfig,
    components: {
      ...muiComponentMapping,
      ...customMapping,
    },
  }).Compiler
  return <div>{renderAst(htmlAst)}</div>
}

export default Markdown
